body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --typography-body-1: Roboto;

  /* font sizes */
  --font-size-sm: 14px;
  --typography-body-1-size: 16px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-26xl: 45px;
  --font-size-7xl: 26px;
  --font-size-lg: 18px;

  /* Colors */
  --color-orange: #fba403;
  --m3-sys-light-on-primary: #fff;
  --color-gray-100: #242424;
  --text-secondary: rgba(255, 255, 255, 0.7);
  --color-darkgray: #b3b3b3;
  --color-darkslategray-100: #383838;

  /* Gaps */
  --gap-xl: 20px;
  --gap-21xl: 40px;
  --gap-base: 16px;
  --gap-5xs: 8px;
  --gap-sm: 14px;
  --gap-11xl: 30px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-5xl: 24px;
  --padding-xs: 12px;
  --padding-21xl: 40px;
  --padding-11xl: 30px;
  --padding-base: 16px;
  --padding-3xl: 22px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-7xs: 6px;
  --br-29xl: 48px;
  --br-9xs: 4px;
}
